.users {
    margin: 2rem auto;
    width: 90%;
    max-width: 40rem;
  }
  
  .users ul {
    list-style: none;
    padding: 1rem;
  }
  
  .users li {
    border: 1px solid #ccc;
    margin: 0.5rem 0;
    padding: 0.5rem;
  }

  .users li:hover {
    border: 1px solid #555;
    background-color: #ddd;
    margin: 0.5rem 0;
    padding: 0.5rem;
    cursor: pointer;
  }